___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --background-black: #181a1b;
    --hover-background-black: #1b1d1e;
    --text-color-beige: #c2a072;
}

html,
body {
    margin: unset;
    min-height: 100%;
    width: 100%;
    position: relative;
    display: flex;
    background: var(--hover-background-black);
    color: var(--text-color-beige);
    font-size: 16px;
    font-family: "Arial";
}

body {
    flex-direction: column;
    align-items: center;
}

/* HEADER */

header {
    width: 100%;
    height: 50px;
    background: #26292b;
}

header nav {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2em;
    height: 100%;
}

header nav button {
    cursor: pointer;
    border: 0px;
    background: #26292b;
    color: var(--text-color-beige);
    font-weight: 300;
    font-size: 18px;
    text-transform: uppercase;
    letter-spacing: 2px;
    height: 50px;
    width: 120px;
}

header nav button:hover, 
header nav button.active {
    background: var(--hover-background-black);
}

#content {
    padding: 20px 0px;
    text-align: center;
    max-width: 600px;
}

/* HOME TAB */

.home-tab .home-image {
    width: 60%;
    height: 60%;
    margin-bottom: 10px;
}

.home-tab .text-box {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 2em;
}

.home-tab .text-box h2 {
    font-size: 32px;
}

.home-tab .extra-info {
    display: flex;
    justify-content: center;
    gap: 2em;
    margin-top: 20px;
}

.home-tab .extra-info .working-hrs,
.home-tab .extra-info .address-text {
    width: 250px;
    text-align: center;
}

.home-tab .extra-info h4 {
    line-height: 46px;
    font-weight: 700;
}

.home-tab .text-box,
.home-tab .extra-info {
    padding: 0px 60px;
}

.home-tab .extra-info p {
    line-height: 20px;
}

/* MENU TAB */

.menu-tab .tab-title {
    font-size: 34px;
    font-weight: 700;
    line-height: 64px;
}

.menu-tab .card-wrapper {
    display: flex;
    align-items: start;
    justify-content: start;
    text-align: start;
    gap: 1em;
    margin: 20px 0px;
    position: relative;
}

.menu-tab .card-wrapper .food-img {
    width: 220px;
    height: 160px;
}

.menu-tab .card-wrapper .food-name {
    font-size: 18px;
    font-weight: 700;
    line-height: 34px;
}

.menu-tab .card-wrapper .main-info .price {
    position: absolute;
    bottom: 0px;
    right: 0px;
}

.menu-tab .card-wrapper .main-info .price::before {
    content: "Price: ";
}

.menu-tab .card-wrapper .main-info .price::after {
    content: " VND";
}

/* CONTACT TAB */

.contact-tab {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1em;
}

.contact-tab .title {
    font-size: 34px;
    font-weight: 700;
    line-height: 64px;
}

.contact-tab .message {
    max-width: 380px;
    margin: 0 auto;
}

.contact-tab .email a,
.contact-tab .phone a {
    color: var(--text-color-beige);
}

.contact-tab .contact-form {
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: .5em;
    width: 350px;
}

.contact-tab .contact-form input,
.contact-tab .contact-form textarea {
    width: 90%;
    border-radius: 10px;
    margin-bottom: 15px;
    font-size: 16px;
    padding: 10px 10px;
}

.contact-tab .contact-form textarea {
    height: 60px;
}

.contact-tab .contact-form .btn-submit {
    width: 100px;
    height: 30px;
    font-size: 18px;
    color:#181a1b;
    margin: 0 auto;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,2BAA2B;IAC3B,iCAAiC;IACjC,2BAA2B;AAC/B;;AAEA;;IAEI,aAAa;IACb,gBAAgB;IAChB,WAAW;IACX,kBAAkB;IAClB,aAAa;IACb,yCAAyC;IACzC,8BAA8B;IAC9B,eAAe;IACf,oBAAoB;AACxB;;AAEA;IACI,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA,WAAW;;AAEX;IACI,WAAW;IACX,YAAY;IACZ,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,QAAQ;IACR,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,WAAW;IACX,mBAAmB;IACnB,8BAA8B;IAC9B,gBAAgB;IAChB,eAAe;IACf,yBAAyB;IACzB,mBAAmB;IACnB,YAAY;IACZ,YAAY;AAChB;;AAEA;;IAEI,yCAAyC;AAC7C;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA,aAAa;;AAEb;IACI,UAAU;IACV,WAAW;IACX,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,QAAQ;IACR,gBAAgB;AACpB;;AAEA;;IAEI,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;;IAEI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA,aAAa;;AAEb;IACI,eAAe;IACf,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,sBAAsB;IACtB,iBAAiB;IACjB,QAAQ;IACR,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,UAAU;AACd;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,eAAe;AACnB;;AAEA,gBAAgB;;AAEhB;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,gBAAgB;IAChB,cAAc;AAClB;;AAEA;;IAEI,8BAA8B;AAClC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,SAAS;IACT,YAAY;AAChB;;AAEA;;IAEI,UAAU;IACV,mBAAmB;IACnB,mBAAmB;IACnB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,eAAe;IACf,aAAa;IACb,cAAc;AAClB","sourcesContent":[":root {\r\n    --background-black: #181a1b;\r\n    --hover-background-black: #1b1d1e;\r\n    --text-color-beige: #c2a072;\r\n}\r\n\r\nhtml,\r\nbody {\r\n    margin: unset;\r\n    min-height: 100%;\r\n    width: 100%;\r\n    position: relative;\r\n    display: flex;\r\n    background: var(--hover-background-black);\r\n 